import React, { Component } from 'react';
import { Timeline, TimelineItem } from 'vertical-timeline-component-for-react';
import data from '../../data.json';
import './timeline.css';

const _ = require('lodash');

export default class BiographyTimeLine extends Component {


  createTimeLineItem = () =>
    _.keys(data.biography).map((e, i) => {
      const dataTimeStyle = { background: 'none' };
  
        dataTimeStyle.background = '#153168';
     
    return (<TimelineItem
      key={i}
      dateText={data.biography[e]["dataTime"]}
      style={{ color: "#153168" }}
      dateInnerStyle={dataTimeStyle}
    ><p>{data.biography[e].data}</p></TimelineItem>); 
  })
  createAdditionalTimeLineItem = () =>
  _.keys(data.AdditionalBiography).map((e, i) => {
    const dataTimeStyle = { background: 'none' };

      dataTimeStyle.background = '#153168';
   
  return (<TimelineItem
    key={i}
    dateText={data.AdditionalBiography[e]["dataTime"]}
    style={{ color: "#153168" }}
    dateInnerStyle={dataTimeStyle}
  >
    <p>{data.AdditionalBiography[e].data}</p>
    <div style={{color: 'black'}}>{data.AdditionalBiography[e].responsibility01}</div>
    <div style={{marginTop: '5px', color: 'black'}}>{data.AdditionalBiography[e].responsibility02}</div>
    <div style={{marginTop: '5px', color: 'black'}}>{data.AdditionalBiography[e].responsibility03}</div>
    <div style={{marginTop: '5px', color: 'black'}}>{data.AdditionalBiography[e].responsibility04}</div>
  </TimelineItem>); 
})
  render() {
    return (
      <>
        <Timeline lineColor={'#000000'}>
          {this.createTimeLineItem()}
          {this.createAdditionalTimeLineItem()}
        </Timeline>
      </>
    );
  }
}
